<template>
  <canvas ref="container" style="width: 100%;height: 100%" :style="{}"></canvas>
</template>

<script>
import * as THREE from "three";
export default {
  name: "threeTexture",
  props: {
    h: Number
  },
  data() {
    return {
      render: null,
      camera: null,
      scene: null,
      player: true,
      cube: null,
      cameraOpt: {
        angle: 70,
        min: 0.1,
        max: 1000
      }
    };
  },
  mounted() {
    console.log(1);
    this.renderAll();
  },
  methods: {
    renderAll() {
      this.renderInit();
      this.renderCube();
      this.animation();
    },
    renderInit() {
      const w = this.$refs.container.clientWidth;
      const h = this.$refs.container.clientHeight;
      this.render = new THREE.WebGLRenderer({
        canvas: this.$refs.container
      });
      this.render.setSize(w, h);
      this.camera = new THREE.PerspectiveCamera(
        this.cameraOpt.angle,
        w / h,
        this.cameraOpt.min,
        this.cameraOpt.max
      );
      this.camera.position.set(0, -80, 80);
      this.camera.lookAt(0, 0, 0);
      this.scene = new THREE.Scene();
    },
    renderCube() {
      const cubeGeometry = new THREE.BoxBufferGeometry(20, 20, 20);
      const texture = new THREE.TextureLoader().load(
        require("@/assets/water.png")
      );
      texture.wrapS = THREE.RepeatWrapping;
      texture.wrapT = THREE.RepeatWrapping;
      texture.repeat.set(4, 4);
      const material = new THREE.MeshBasicMaterial({
        map: texture,
        transparent: true
      });
      this.cube = new THREE.Mesh(cubeGeometry, material);
      this.scene.add(this.cube);
    },
    animation() {
      if (this.cube) {
        this.cube.rotation.x += 0.01;
        this.cube.rotation.y += 0.01;
      }
      this.render.render(this.scene, this.camera);
      requestAnimationFrame(() => {
        if (this.player) {
          this.animation();
        }
      });
    }
  }
};
</script>

<style scoped></style>
